/** Happy Coding */
@import '~_sass/vars';

// $color: theme color:
// $color-{white,black}-{1..9}0: lighten or darken
// $color-font-head: heading color
// $color-font: text main color
// $color-font-gray: text secondary color
// $color-border: border color
// $color-background: background color
// $color-{fail,okay,warn,info,dead}: status colors
// `box-shadow`: $box-shadow

.draw-node {
  &-main {
    display: flex;
    transform-origin: center;
    box-sizing: content-box;
  }
  &-focus {
    background: pink !important;
    box-shadow: none !important;
    & > div {
      opacity: 0.618;
    }
  }
  &-grid {
    @for $i from 0 to 81 {
      & > :nth-child(#{1 + $i}) {
        grid-area: a#{$i};
      }
    }
  }
}
